  /* Define ship color variables */
  --blue-border: 2px solid blue;
  --ship-background: rgb(197, 226, 238);
}

* {
  margin: 0;
  border: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

body {
  display: flex;
  height: 100vh;
  flex-direction: column;
  background-color: rgb(124, 194, 255);
}

header {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  font-size: 24px;
  color: white;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: black;
}

main {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

footer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 0px;
  gap: 5px;
  font-size: 20px;
  font-weight: 600;
  color: white;
}

a {
  color: inherit;
  text-decoration: none;
}

.gameboard {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(10, 1fr);
  margin-bottom: 20px;
  width: 400px;
  height: 400px;
  border: 2px solid rgb(170, 165, 165);
}

.gameboard button {
  border: 1px solid rgb(170, 165, 165);
  background-color: rgb(236, 231, 231);
  cursor: pointer;
  position: relative;
}

/* Start of ship, X-axis */
.gameboard .start-ship-x {
  border-top: var(--blue-border);
  border-bottom: var(--blue-border);
  border-left: var(--blue-border);
  border-right: var(--ship-background);
  background-color: var(--ship-background) !important;
}

/* Middle of ship, X-axis */
.gameboard .mid-ship-x {
  border-top: var(--blue-border);
  border-bottom: var(--blue-border);
  border-left: var(--ship-background);
  border-right: var(--ship-background);
  background-color: var(--ship-background) !important;
}

/* End of ship, X-axis */
.gameboard .end-ship-x {
  border-top: var(--blue-border);
  border-bottom: var(--blue-border);
  border-left: var(--ship-background);
  border-right: var(--blue-border);
  background-color: var(--ship-background) !important;
}

/* Start of ship, Y-axis */
.gameboard .start-ship-y {
  border-top: var(--blue-border);
  border-left: var(--blue-border);
  border-right: var(--blue-border);
  border-bottom: var(--ship-background);
  background-color: var(--ship-background) !important;
}

/* Middle of ship, Y-axis */
.gameboard .mid-ship-y {
  border-top: var(--ship-background);
  border-bottom: var(--ship-background);
  border-left: var(--blue-border);
  border-right: var(--blue-border);
  background-color: var(--ship-background) !important;
}

/* End of ship, Y-axis */
.gameboard .end-ship-y {
  border-top: var(--ship-background);
  border-bottom: var(--blue-border);
  border-left: var(--blue-border);
  border-right: var(--blue-border);
  background-color: var(--ship-background) !important;
}

/* Ship placement styles */
.ship-placement-ctn {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.instructions-heading {
  color: rgb(255, 255, 255);
  font-size: 34px;
  margin-bottom: 20px;
}

.toggle-axis,
.controls-ctn button,
.play-again-btn {
  padding: 10px;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  margin-bottom: 20px;
  border: 1px solid black;
  color: black;
}

.controls-ctn {
  display: flex;
  align-items: center;
  gap: 15px;
}

/* Game start styles */
.friendly-ctn,
.enemy-ctn {
  display: flex;
  flex-direction: column;
  width: 400px;
}

.board-label {
  color: white;
  text-transform: uppercase;
  font: 14px;
}

.friendly-label {
  color: rgb(20, 62, 179);
}

.enemy-label {
  color: red;
}

.game-start-ctn .instructions-heading {
  text-align: center;
}

.gameboards-ctn {
  display: flex;
  gap: 5vw;
}

.winner-ctn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.winner-heading {
  color: white;
  margin-bottom: 10px;
}

.enemy-board button:hover {
    background-color: rgb(206, 199, 199);
}

.miss::after {
  content: '';
  width: 6px;
  height: 6px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -4px;
  border-radius: 50%;
  background-color: #000000a9;
}

.hit {
  border: 2px solid red !important;
  margin: 0;
}

.hit::after {
  content: '✕';
  color: red;

  font-size: 2em;
  font-weight: 600;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Modal styles */
.modal {
  display: block;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
}

/* Modal Content/Box */
.modal-content {
  background-color: #fefefe;
  max-width: 200px;
  margin: 15% auto;
  border: 1px solid #888;
  padding: 20px;
  border-radius: 15px;
  text-align: center;
}

.modal-content * {
  color: black;
  margin: 0;
}

.modal-content .winner-heading {
  margin-bottom: 10px;
}

/* Media Queries */
@media only screen and (max-width: 854px) {
  .gameboards-ctn {
    flex-direction: column-reverse;
    justify-content: center;
    text-align: center;
  }
}
